export default () => {
  return new Promise((resolve, reject) => {
    window.fetch("https://api.bastionbot.org/paypal/donors", {
      headers: {
          "User-Agent": "The Bastion Bot Website (https://bastionbot.org)",
      },
    })
    .then(res => res.json())
    .then(data => {
      resolve(data);
    })
    .catch(e => reject(e));
  });
}
